*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body,
input,
button,
textarea,
select {
  font-size: 16px;
  font-family: sans-serif;
}

body {
  @include flex(center, center, column);
  background-color: #41C184;
}

ul {
  list-style: none;
}

a, button {
  background-color: transparent;
  border: none;
  outline: none;
  display: inline-flex;
  text-decoration: none;
}
